@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Nunito', sans-serif;
}

:root {
  --nav-color: hsl(210, 11%, 15%);
  --nav-color-hover: hsl(210, 11%, 45%);
  --nav-border-color: hsl(210, 11%, 45%);
  --light-color: hsl(209, 100%, 92%);
  --font-color: hsl(0, 0%, 100%);
  --font-color-hover: hsl(0, 0%, 40%);
}
.navigation__container {
  background-color: var(--nav-color);
  color: white
}

.navbar-light .navbar-nav .nav-link {
  color: var(--font-color) !important;
}

.navbar-light .navbar-brand {
  color: var(--font-color) !important;
}

.navbar-light .navbar-toggler-icon {
  -webkit-filter: invert(1);
          filter: invert(1);
}

.links a:hover {
  background-color: var(--font-color-hover);
}
.main-info {
  font-family: sans-serif;
  font-weight:800;
  font-size:40px;
  text-align: center;
  display:flex;
  justify-content:center;
  align-items: center;
  min-height:100vh;
  background: var(--light-color);
}
.about__container {
  margin: 3rem 0;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.about__container img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 0.5px solid rgb(218,218,218);
  margin-bottom: 1rem;
}


.about__text {
  max-width: 600px;
}

/*  ---------------- MEDIA QUERIES ----------- */

@media only screen and (max-width: 900px ){
  .about__container {
    flex-direction: column;
  }
}

@media only screen and (max-width: 700px ){
  .about__text {
    max-width: 300px;
  }
}
.technologies__container {
  background-color: var(--light-color);
  color: var(--nav-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem;
}

.tech__cards__container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1100px;
  margin: 1rem 0;
}

.tech__card {
  width: 15rem;
  height: 10rem;
  background-color: var(--font-color);
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
  border-bottom: 5px solid var(--nav-color);
  transition: all 0.1s;
}

.tech__icons {
  height: 70px;
  background-color: var(--light-color);
  border-radius: 50%;
}

.tech__card:hover {
  background-color: var(--font-color-hover);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.portfolio {
  background-color: aqua;
}

.portfolio__main__container h1, 
.portfolio__main__container p {
  text-align: center;
  margin-top: 3rem;
}

#portfolio__card__container {
  width: 50%;
  padding: 10px;
  border: none;
}

.portfolio__image:hover {
  cursor: pointer;
  -webkit-filter: brightness(50%);
          filter: brightness(50%);
}

.portfolio__click__info {
  position: absolute;
  top: 50%;  
  left: 50%; 
  -webkit-transform: translate(-50%, -50%); 
          transform: translate(-50%, -50%);
  color: var(--font-color);
  display: none;
}

.portfolio__image:hover + .portfolio__click__info {
  display: block;
}

.modal-backdrop.show {
  opacity: .1 !important;
}

#portfolio__modal__link {
  padding: 0 0 1rem 1rem
}

#portfolio__modal__link:link {
  text-decoration: none;
}

#portfolio__modal__repo {
  padding: 0 0 1rem 1rem
}

#portfolio__modal__repo:link {
  text-decoration: none;
}


@media only screen and (max-width: 600px){
	#portfolio__card__container {
    width: 100%;
    padding: 0.5rem 2.5rem;
    border: none;
  }
}
.resume__container {
  background-color: rgb(229, 247, 146);
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.resume__container img {
  width: 200px;
  height: 200px;
  margin-bottom: 1rem;
  margin-top: 1rem;
}


.resume__text {
  max-width: 600px;
}

/*  ---------------- MEDIA QUERIES ----------- */

@media only screen and (max-width: 900px ){
  .resume__container {
    flex-direction: column;
  }
}

@media only screen and (max-width: 700px ){
  .resume__text {
    max-width: 300px;
  }
}
.sendMessage__container {
  background: #fff;
  box-shadow: 0 30px 60px 0 rgba(90, 116, 148, 0.4);
  border-radius: 5px;
  max-width: 480px;
  margin-left: 3rem;
  margin-top: 1rem;
  margin-bottom: 3rem;
  padding-top: 5px;
  padding-bottom: 5px;
  left: 0;
  right: 0;
  position: absolute;
  border-top: 5px solid #0e3721;
  /* z-index: 1; */
  -webkit-animation: bounce 1.5s infinite;
          animation: bounce 1.5s infinite;
}


@-webkit-keyframes bounce {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}


@keyframes bounce {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}




/*  ---------------- MEDIA QUERIES ----------- */

@media only screen and (max-width: 900px ){
  .sendMessage__container {
    flex-direction: column;
  }
}

@media only screen and (max-width: 700px ){
  .sendMessage__text {
    max-width: 300px;
  }
}
.contactMe__container {
  background-color: var(--nav-color);
  color: aliceblue;
  padding: 1rem;
}

.contactMe__data__links {
  display: flex;
  justify-content: space-evenly;
}

.contactMe__data__links a {
  color: white;
  text-decoration: none;
  text-transform: uppercase;
}

.contactMe__data__links a:hover {
  color: var(--light-color);
}

@media only screen and (max-width: 600px){
  .contactMe__data__links {
    flex-direction: column;
  }
}
